@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans Bold";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}
body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans", sans-serif, "Arial";
  font-size: 14px;
  line-height: 21px;
  background-color: #e7e5e5;
  color: #000000;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
.page__container {
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .page__container {
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    min-height: 100%;
    margin: 0 auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  min-height: 56px;
  position: relative;
}
.logo__header {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
}
.header__nav {
  display: flex;
  min-width: 320px;
}
.nav__toggle {
  width: 23px;
  height: 23px;
  margin: 17px 20px 17px auto;
  background-image: url("../img/icon-menu.svg");
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.nav-list {
  display: flex;
  flex-direction: column;
  list-style: none;
  text-decoration: none;
  margin: 18px auto;
}
.nav-list--hidden {
  display: none;
}
@media (min-width: 768px) {
  .header__nav {
    display: inline-block;
    width: 100%;
  }
  .nav-list--hidden {
    display: contents;
  }
  .nav__toggle--hidden {
    display: none;
  }
  .nav-list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    min-width: 768px;
    padding: 0;
  }
  .nav-list__item {
    justify-self: center;
    cursor: pointer;
  }
  .item3 {
    grid-column-start: 4;
  }
  .nav-list__link {
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    text-decoration: none;
    color: #000000;
  }
  .nav-list__item--current {
    cursor: default;
  }
  .nav-list__link--current {
    color: #766357;
  }
}
